import React from 'react';
import PropTypes from 'prop-types';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import {
  Row,
  Col,
  Form,
  FormGroup,
  Input,
  Button,
  ButtonGroup,
  Alert,
  Label,
  Breadcrumb,
  BreadcrumbItem,
} from 'reactstrap';
import { connect } from 'react-redux';

import Widget from '../../../components/Widget';

import { createPost } from '../../../actions/posts';
import s from './PostNew.module.scss';
import axios from 'axios';

class PostNew extends React.Component {
  static propTypes = {
    message: PropTypes.string,
    isFetching: PropTypes.bool,
  };

  static defaultProps = {
    isFetching: false,
    message: null,
  };

  constructor(props) {
    super(props);

    this.state = {
      username: '',
      password: '',
    };
  }

  changeUsername = (event) => {
    this.setState({username: event.target.value});
  }

  changePassword = (event) => {
    this.setState({password: event.target.value});
  }

  AddUser = () => {
    axios.post('http://localhost:9000/admin/user/add', {
      "id": 3,
      "username": "Marco",
      "firstName": "",
      "lastName": "",
      "created": "2021-04-28T13:24:36.214Z",
      "email": "",
      "password": ""
  }).then(response => console.log(response))
  }

  doCreatePost = (e) => {
    axios.post('http://localhost:9000/admin/user/add', {
      "id": 3,
      "username": this.state.username,
      "firstName": "",
      "lastName": "",
      "created": "2021-04-28T13:24:36.214Z",
      "email": "",
      "password": this.state.password
      })
      .then(response => console.log(response))
      .then(() => toast.success('Successfully added user ' + this.state.username, {
        position: "bottom-right",
        autoClose: 5000,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true
      }))
      .then(() =>
        this.setState({
          username: '',
          password: '',
        }),
      );
    e.preventDefault();
  }

  render() {
    return (
      <div className={s.root}>
            <Widget
              title={
                <span>
                  Add Post <span className="fw-semi-bold">Form</span>
                </span>
              }
            >
              <Form onSubmit={this.doCreatePost}>
                {this.props.message && (
                  <Alert className="alert-sm" bsstyle="info">
                    {this.props.message}
                  </Alert>
                )}
                <FormGroup>
                  <Label for="input-username">Username</Label>
                  <Input
                    id="input-username"
                    type="text"
                    placeholder="username"
                    value={this.state.username}
                    required
                    onChange={this.changeUsername}
                  />
                </FormGroup>
                <FormGroup>
                  <Label for="input-password">Password</Label>
                  <Input
                    id="input-password"
                    type="password"
                    placeholder="password"
                    value={this.state.password}
                    required
                    onChange={this.changePassword}
                  />
                </FormGroup>
                <div className="d-flex justify-content-end">
                  <ButtonGroup>
                    <Button color="default">Cancel</Button>
                    <Button color="primary" type="submit">
                      {this.props.isFetching ? 'Creating...' : 'Create'}
                    </Button>
                  </ButtonGroup>
                </div>
              </Form>
            </Widget>
      </div>
    );
  }
}

export default PostNew;
